
const formatTime = time => {
  const date = new Date(time*1000);
  const year = date.getFullYear()
  const month = date.getMonth() + 1
  const day = date.getDate()
  const hour = date.getHours()
  const minute = date.getMinutes()
  const second = date.getSeconds()
  return [year, month, day].map(formatNumber).join('-') + ' ' + [hour, minute, second].map(formatNumber).join(':')
}



const htmlEscapt = function (html) {
  var reg = /(&lt;)|(&gt;)|(&amp;)|(&quot;)/g;
  html = html.replace(reg, function (match) {
    switch (match) {
      case "&lt;":
        return "<";
      case "&gt;":
        return ">"
      case "&amp;":
        return "&";
      case "&quot;":
        return "\""
    }
  });

  return html.replace(/\<img/g, '<img style="max-width:100%;height:auto"');
}

const formatNumber = n => {
  n = n.toString()
  return n[1] ? n : '0' + n
}

const formatDuring = time => {
  const days = parseInt(time / (1000 * 60 * 60 * 24));
  const hours = parseInt((time % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)) ;
  const minutes = parseInt((time % (1000 * 60 * 60)) / (1000 * 60));
  const seconds = parseInt((time % (1000 * 60)) / 1000);
  return {days,hours,minutes,seconds};
};
/**
 * 生成富文本节点
 */
function generateRichTextNode(text) {
  var emoji = emojimap.emojiList.emoji;
  let tempStr = text
  let richTextNode = []
  let leftBracketIndex = tempStr.indexOf('[')
  let rightBracketIndex = tempStr.indexOf(']')
  let countOfWord = 0
  Array.from(tempStr).map(item => {
    if (item != '[' && item != ']') {
      countOfWord++
    }
  })
  if (leftBracketIndex == -1 || rightBracketIndex == -1 || countOfWord == 0) {//没有emoji
    richTextNode.push({
      type: 'text',
      text: tempStr
    })
    return richTextNode
  }
  while (tempStr.length != 0) {
    leftBracketIndex = tempStr.indexOf('[')
    rightBracketIndex = tempStr.indexOf(']')
    if (leftBracketIndex == 0) { // 开头是[
      rightBracketIndex = tempStr.indexOf(']')
      if (rightBracketIndex == -1) {
        richTextNode.push({
          type: 'text',
          text: tempStr
        })
        tempStr = ''
      } else {
        let emojiName = tempStr.slice(0, rightBracketIndex + 1)
        if (emoji[emojiName]) { // 有效emoji
          richTextNode.push({
            name: 'img',
            attrs: {
              width: '30rpx',
              height: '30rpx',
              src: emoji[emojiName].img
            }
          })
        } else {//无效emoji
          richTextNode.push({
            type: 'text',
            text: emojiName
          })
        }
        tempStr = tempStr.substring(rightBracketIndex + 1, tempStr.length)
      }
    } else { // 开头不是[
      if (leftBracketIndex == -1) {// 最后全是文字
        richTextNode.push({
          type: 'text',
          text: tempStr.slice(0, tempStr.length)
        })
        tempStr = ''
      } else {
        richTextNode.push({
          type: 'text',
          text: tempStr.slice(0, leftBracketIndex)
        })
        tempStr = tempStr.substring(leftBracketIndex, tempStr.length + 1)
      }
    }
  }
  return richTextNode
}

module.exports = {
  formatTime,
  formatDuring,
  htmlEscapt,
}



